import { useEffect, useState } from 'react';
import ReactMarkdown from 'react-markdown';
import remarkGfm from 'remark-gfm';
import rehypeSlug from 'rehype-slug';
import rehypeAutolinkHeadings from 'rehype-autolink-headings';
import Prism from 'prismjs';
import 'prismjs/themes/prism.css';
import 'prismjs/components/prism-typescript';
import 'prismjs/components/prism-javascript';
import 'prismjs/components/prism-json';
import 'prismjs/components/prism-bash';
import 'prismjs/components/prism-powershell';
import { GitHubPost } from '../model/GithubPost';
import { PostContent } from '../model/PostContent';
import fm from 'front-matter';

export default function PostSection() {
    const [posts, setPosts] = useState<PostContent[] | []>([]);
    const [postOpened, setPostOpened] = useState<PostContent | null>(null);

    useEffect(() => {
        fetch('https://api.github.com/repos/Ludwikster/thedataversegrimoire.github.io/contents/_posts')
            .then(res => res.json())
            .then(async (files) => {
                const githubPostsArray = files.map((item: any) => new GitHubPost(item));
                debugger;
                const postsWithContent = await Promise.all(
                    githubPostsArray.map(async (githubPost: GitHubPost) => {
                        const res = await fetch(githubPost.download_url);
                        const md: string = await res.text();

                        const parsed = fm<any>(md);
                        const data = parsed.attributes;
                        const content = parsed.body;

                        const title = data.title ?? githubPost.name ?? 'Untitled';
                        const date = data.date ?? githubPost.date ?? new Date().toISOString();
                        const tags = Array.isArray(data.tags) ? data.tags : (data.tags ? [data.tags] : []);
                        return new PostContent(title, date, content, tags);
                    })
                );

                const filtered = postsWithContent.filter(p => !p.tags.includes('intro'));
                setPosts(filtered);
            })
            .catch(console.error);
    }, []);

    // Re-run syntax highlighting when open post changes (after markdown rendered)
    useEffect(() => {
        if (postOpened) {
            // Wait for the DOM to paint, then highlight
            const id = requestAnimationFrame(() => Prism.highlightAll());
            return () => cancelAnimationFrame(id);
        }
    }, [postOpened]);

    return (
        <section className="post-section">
            <div className="post-content">
                {posts.map((post, index) => (
                    <article key={index} onClick={() => setPostOpened(post)}>
                      <h3>{post.name}</h3>
                        <p><em>{new Date(post.createdDate).toISOString().slice(0, 10)}</em></p>
                        <p>{post.tags.join(', ')}</p>
                        {postOpened === post && (
                            <section
                                className="post-body"
                                style={{ maxHeight: '60vh', overflow: 'auto', marginTop: '0.5rem' }}
                                onClick={(e) => e.stopPropagation()}
                            >
                                <ReactMarkdown
                                    remarkPlugins={[remarkGfm]}
                                    rehypePlugins={[
                                        rehypeSlug,
                                        rehypeAutolinkHeadings,
                                    ]}
                                    components={{
                                        code({ className, children, ...props }) {
                                            const match = /language-(\w+)/.exec(className || '');
                                            if (match) {
                                                return (
                                                    <pre className={`language-${match[1]}`}>
                                                        <code className={`language-${match[1]}`} {...props}>
                                                            {String(children).replace(/\n$/, '')}
                                                        </code>
                                                    </pre>
                                                );
                                            }
                                            return (
                                                <code className={className} {...props}>
                                                    {children}
                                                </code>
                                            );
                                        },
                                        img({ src, alt }) {
                                            return (
                                                <img
                                                    src={src as string}
                                                    alt={alt ?? ''}
                                                    style={{ maxWidth: '100%', height: 'auto', borderRadius: 8 }}
                                                    loading="lazy"
                                                    decoding="async"
                                                />
                                            );
                                        },
                                        a({ href, children, ...props }) {
                                            return (
                                                <a href={href} target="_blank" rel="noopener noreferrer" {...props}>
                                                    {children}
                                                </a>
                                            );
                                        },
                                    }}
                                >
                                    {post.content}
                                </ReactMarkdown>
                            </section>
                        )}
                    </article>
                ))}
            </div>
        </section>
    );
}
