export class GitHubPost {
  name: string;
  path: string;
  sha: string;
  size: number;
  url: string;
  html_url: string;
  git_url: string;
  download_url: string;
  type: string;
  date: Date;
  _links: {
    self: string;
    git: string;
    html: string;
  };

  constructor(data: any) {
    this.name = data.name;
    this.path = data.path;
    this.sha = data.sha;
    this.size = data.size;
    this.url = data.url;
    this.html_url = data.html_url;
    this.git_url = data.git_url;
    this.download_url = data.download_url;
    this.type = data.type;
    this.date = data.date;
    this._links = {
      self: data._links.self,
      git: data._links.git,
      html: data._links.html,
    };
  }
}